
import $ from 'jquery';

class DisplayError {

  constructor(el, error) {
    this.el = el;
    this.error = error;
    this.rootElement = _.has(el, 'length') ? el[0] : el;
  }

  draw() {
    const errorContainer = $('<div class="rhtml-error-container">');

    const errorImage = $(`<img width="32px" height="32px" src="${this.dataUri()}"/>`);

    const errorText = $('<span>')
      .html(this.error.toString());

    errorContainer.append(errorImage);
    errorContainer.append(errorText);

    $(this.rootElement).empty();
    $(this.rootElement).append(errorContainer);
  }

  drawSvg() {
    this.el.append('image')
      .attr('xlink:href', this.dataUri())
      .attr('x', 0)
      .attr('y', 0)
      .attr('width', 50)
      .attr('height', 50)
      .append('title')
      .text(this.error);

    this.el.append('text')
      .style('fill', 'red')
      .attr('x', 50)
      .attr('y', 50)
      .text(this.error);
  }

  dataUri() {
    return 'data:image/png;base64,';
  }
}

module.exports = DisplayError;
